:root {
  --blue: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

header.cabecalho {
  background-color: red;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 16px 32px;
  display: flex;
  position: relative;
  left: 0;
}

.cabecalho img {
  height: 40px;
  top: 10px;
  left: 50px;
}

nav.container ul {
  text-transform: uppercase;
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

li:not(:first-child) {
  margin-left: 32px;
}

nav.container a {
  text-transform: uppercase;
  height: 40px;
  font-weight: 500;
  line-height: 40px;
  text-decoration: none;
}

nav.container a:hover {
  -webkit-text-decoration: underline var(--blue);
  text-decoration: underline var(--blue);
}

[data-qa="hover"] {
  color: var(--blue);
}

.is-active {
  position: relative;
}

.is-active:after {
  background-color: var(--blue);
  content: "";
  border-radius: 10px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 30px;
  bottom: -4px;
  left: 0;
}
